
.app-container {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #fefefe;
  border-radius: 12px;
  box-shadow: 0 8px 24px rgba(0,0,0,0.1);
  color: #222;
  display: flex;
  flex-direction: column;
  min-height: 80vh;
}

.app-header {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
}

.app-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin: 0;
  color: #333;
}


.app-container.dark {
  background-color: #121212;
  color: #e0e0e0;
  box-shadow: 0 8px 24px rgba(255, 255, 255, 0.05);
}


.app-container.dark .app-title {
  color: #eee;
}


.app-container.dark button {
  background-color: #333;
  color: #eee;
  border-color: #555;
}


.app-container.dark button:hover {
  background-color: #555;
}


.app-container.dark input[type="text"] {
  background-color: #222;
  color: #eee;
  border-color: #555;
}


.input-group {
  display: flex;
  gap: 12px;
  margin-bottom: 24px;
}

.input-group input[type="text"] {
  flex-grow: 1;
  padding: 10px 16px;
  font-size: 1.1rem;
  border: 2px solid #ccc;
  border-radius: 8px;
  transition: border-color 0.3s ease;
}

.input-group input[type="text"]:focus {
  border-color: #1e90ff;
  outline: none;
}


button {
  padding: 10px 20px;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 8px;
  border: 2px solid #1e90ff;
  background-color: transparent;
  color: #1e90ff;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
  user-select: none;
}

button:hover {
  background-color: #1e90ff;
  color: white;
}


.task-list {
  list-style: none;
  padding: 0;
  margin: 0 0 32px 0;
  flex-grow: 1;
  overflow-y: auto;
}

.task-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fafafa;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 12px 16px;
  margin-bottom: 10px;
  transition: background-color 0.3s ease;
}

.task-item:hover {
  background-color: #f0f8ff;
}


.app-container.dark .task-item {
  background-color: #1e1e1e;
  border-color: #333;
}

.app-container.dark .task-item:hover {
  background-color: #2c2c2c;
}


.completed {
  text-decoration: line-through;
  color: #888;
}


.app-container.dark .completed {
  color: #777;
}

.task-buttons {
  display: flex;
  gap: 8px;
}


.complete-btn, .delete-btn {
  padding: 6px 12px;
  font-size: 1.1rem;
  font-weight: 700;
  border-radius: 6px;
  border: none;
  cursor: pointer;
  user-select: none;
  transition: background-color 0.25s ease;
}


.complete-btn {
  background-color: #4caf50;
  color: white;
}

.complete-btn:hover {
  background-color: #45a045;
}


.delete-btn {
  background-color: #f44336;
  color: white;
}

.delete-btn:hover {
  background-color: #d32f2f;
}


.footer {
  display: flex;
  justify-content: center;
  gap: 12px;
  padding-top: 12px;
  border-top: 1px solid #ddd;
}

.footer button {
  border-color: #888;
  color: #555;
  padding: 8px 18px;
  font-weight: 600;
  font-size: 1rem;
  background-color: transparent;
  border-radius: 10px;
  transition: all 0.3s ease;
}

.footer button:hover {
  background-color: #1e90ff;
  color: white;
  border-color: #1e90ff;
}

.footer button.active {
  background-color: #1e90ff;
  color: white;
  border-color: #1e90ff;
}


.app-container.dark .footer {
  border-top-color: #333;
}

.app-container.dark .footer button {
  border-color: #555;
  color: #aaa;
}

.app-container.dark .footer button:hover {
  background-color: #2a79d8;
  color: white;
  border-color: #2a79d8;
}

.app-container.dark .footer button.active {
  background-color: #2a79d8;
  color: white;
  border-color: #2a79d8;
}


.dark-mode-toggle {
  margin-left: auto;
  padding: 8px 16px;
  font-size: 0.9rem;
  cursor: pointer;
  background: transparent;
  border: 2px solid #1e90ff;
  color: #1e90ff;
  border-radius: 8px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.dark-mode-toggle:hover {
  background-color: #1e90ff;
  color: white;
}
